import React , { Component } from 'react';
import './App.css';

import Item from '../item/item';
import Navigation from '../navigation/navigation';
import { textData }  from './text'

class App extends Component {
  textData = this.textData;
  originId = 1;

  state = {
    appData : [
      this.createElement(''),
      this.createElement('japan'),
      this.createElement('france'),
      this.createElement('mexico'),
      this.createElement('finland'),
      this.createElement('italy'),
      this.createElement('usa'),
      this.createElement('uae'),
      this.createElement('thailand'),
      this.createElement('germany'),
    ],
    currentId: 1,
  }

  onNavClick = (newId) => {
    this.setState({
      currentId: newId
    })
  }

  onAddId = () => {
    this.setState(({currentId}) => {
      if (currentId < 10) {
      return {
        currentId: currentId+1
      }
    }
    })
  }

  createElement(name) {
    return {
      name,
      text: textData[this.originId-1],
      id: this.originId++,
    }
  }

  render() {
    const { appData , currentId} = this.state;

    const currentItem = appData.find((item) => item.id === currentId)
    return (
      <div className="app">
        <Navigation data={appData} onNavClick={this.onNavClick} currentId={currentId}/>
        <Item itemData={currentItem} onAddId={this.onAddId} />
      </div>
    )
  }
}

export default App;
