import React  from 'react';

import './item.css';

const Item = ({itemData, onAddId}) => {

  const {name, text} = itemData;
  let className = 'myBox d-flex '+ name;

  return(
    <div className={className} onDoubleClick={onAddId} >
      <div className="container" >
      <h2>{name}</h2>
      {text}
      </div>
    </div>
  )

}
export default Item;
