import React  from 'react';


import './navigation.css';
const Navigation = ({data, onNavClick, currentId}) => {

  const items = data.map((item) => {

    const { id } = item;
    let clazz = 'btn-light';
    if(id === currentId) clazz = 'btn-primary';

    return (
      <button key={id}
              className={`btn myBtn ${clazz}`}
              onClick={() => onNavClick(id)}>
        {id}
      </button>
    )
  });

  return (
    <div className="navigation d-flex justify-content-around ">
      {items}
    </div>
  )

};

export default Navigation;
